  display: none;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.align-right {
  display: flex;
  justify-content: flex-end;
}

.btn-amarillo-block {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  gap: 2.5rem;
  display: block;
  border: none;
  cursor: pointer;
}
.btn-amarillo-block:hover {
  background-color: #c77808;
}

.btn-amarillo-inline {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  gap: 2.5rem;
  display: inline-block;
  border: none;
  cursor: pointer;
}
.btn-amarillo-inline:hover {
  background-color: #c77808;
}

.btn-verde {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  gap: 2.5rem;
  display: inline-block;
  border: none;
  cursor: pointer;
  margin: 2rem 0 2rem;
}
.btn-verde:hover {
  background-color: #619800;
}

.dark-mode {
  background-color: #1a1a1a;
}
.dark-mode p {
  color: #FFFFFF;
}
.dark-mode a, .dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4 {
  color: #e1e1e1;
}
.dark-mode .anuncio {
  background-color: #333333;
  border: 1px solid #333333;
}
.dark-mode .anuncio .price {
  color: #71B100;
}
.dark-mode .icons-features img {
  filter: invert(100%);
}

/* UI */
.no-webp .inicio {
  background-image: url(/build/img/header.jpg);
}

.webp .inicio {
  background-image: url(/build/img/header.webp);
}

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 60rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .header-content {
    min-height: 60rem;
  }
}

.header-content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.header-content h1 {
  text-align: left;
  color: #FFFFFF;
  font-weight: 700;
  line-height: 2;
  max-width: 60rem;
}

.footer {
  padding: 2.5rem;
  background-color: #333333;
  margin: 2rem 0 0 0;
}
.footer .footer-content {
  padding-top: 2.5rem;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
}

.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .derecha {
    align-items: flex-end;
  }
}

.dark-mode-btn {
  width: 3rem;
  margin: 3rem 0;
  filter: invert(100%);
}
@media (min-width: 768px) {
  .dark-mode-btn {
    margin-top: 0;
  }
}
.dark-mode-btn:hover {
  cursor: pointer;
}

.bar {
  display: flex;
  flex-direction: column;
  padding: 3rem;
  align-items: center;
}
@media (min-width: 768px) {
  .bar {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
  }
}

.navbar {
  visibility: hidden;
  opacity: 0;
  /* Creando animaciones con visibility y opacity */
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear;
}
@media (min-width: 768px) {
  .navbar {
    display: flex;
    align-items: center;
    opacity: 1;
    visibility: visible;
  }
}
.navbar a {
  color: #FFFFFF;
  font-size: 2.2rem;
  text-decoration: none;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navbar a {
    margin-right: 2rem;
    font-size: 1.8rem;
  }
  .navbar a:last-of-type {
    margin: 0;
  }
}

@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
}

.mostrar {
  visibility: visible;
  opacity: 1;
}

@media (min-width: 768px) {
  .icons-ous {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.icon {
  text-align: center;
}
.icon img {
  height: 10rem;
}
.icon h3 {
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .add-container {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.anuncio {
  background-color: #eeeeee;
  border: 1px solid #4d4d4d;
}
.anuncio .add-content {
  padding: 2rem;
}
.anuncio h3 {
  min-height: 10.8rem;
}
.anuncio h3, .anuncio p {
  margin: 0;
}

.price {
  color: #71B100;
  font-size: 3rem;
  font-weight: 700;
}

.icons-features {
  display: flex;
  list-style: none;
  padding: 0;
  max-width: 40rem;
}
.icons-features li {
  flex: 1;
  display: flex;
}
.icons-features li img {
  flex: 0 0 3rem;
  margin-right: 1rem;
}
.icons-features li p {
  font-weight: 900;
}

.no-webp .imagen-contacto {
  background-image: url(/build/img/encuentra.jpg);
}

.webp .imagen-contacto {
  background-image: url(/build/img/encuentra.webp);
}

.imagen-contacto {
  background-position: center center;
  background-size: cover;
  padding: 5rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.imagen-contacto h2, .imagen-contacto p {
  color: #FFFFFF;
}
.imagen-contacto h2 {
  font-size: 4rem;
  font-weight: 900;
}
.imagen-contacto p {
  font-size: 1.8rem;
}

@media (min-width: 768px) {
  .seccion-inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
.meta-info span {
  color: #E08709;
}

.entrada-blog {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .entrada-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.entrada-blog:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .entrada-blog .imagen {
    margin-top: 2rem;
  }
}

.text-entrada a {
  color: #000000;
}
.text-entrada h4 {
  margin: 2rem 0 0 0;
  line-height: 1.4;
}
.text-entrada h4::after {
  content: "";
  margin-top: 1rem;
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #71B100;
}

.testimonial {
  background-color: #71B100;
  font-size: 2.4rem;
  padding: 2rem;
  color: #FFFFFF;
  border-radius: 2rem;
}
.testimonial blockquote {
  padding-left: 5rem;
  position: relative;
}
.testimonial blockquote::before {
  content: "";
  background-image: url(/build/img/comilla.svg);
  background-repeat: no-repeat;
  width: 6rem;
  height: 8rem;
  display: block;
  position: absolute;
  left: -2rem;
}
.testimonial p {
  text-align: right;
  color: #FFFFFF;
}

.form {
  /* No selecionar un input especifico */
}
.form p {
  font-size: 1.4rem;
  color: #333333;
  margin: 2rem 0 0 0;
}
.form legend {
  color: #333333;
  font-size: 2rem;
}
.form label {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
}
.form input:not([type=submit]),
.form textarea,
.form select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.form .contact-way {
  display: flex;
  max-width: 30rem;
  justify-content: space-between;
  align-items: center;
}
.form input[type=radio] {
  margin: 0;
  width: auto;
}

/* Paginas internarls */
.contenido-nosotros {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .contenido-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.texto-nosotros blockquote {
  font-weight: 900;
  font-size: 2.2rem;
  margin: 0;
  padding: 1rem 0 3rem 0;
}