.form{
    p{
        font-size: 1.4rem;
        color: $grisOscuro;
        margin: 2rem 0 0 0 ;
    }
    legend{
        color: $grisOscuro;
        font-size: 2rem;
    }
    label{
        font-weight: $bold;
        text-transform: uppercase;
        display: block;
    }
    /* No selecionar un input especifico */
    input:not([type="submit"]),
    textarea,
    select{
        padding: 1rem;
        display: block;
        width: 100%;
        background-color: $blanco;
        border: 1px solid $gris;
        border-radius: 1rem;
        margin-bottom: 2rem;
    }
    .contact-way{
        display: flex;
        max-width: 30rem;
        justify-content: space-between;
        align-items: center;
    }
    input[type="radio"]{
        margin: 0;
        width: auto;
    }
}